<template>
    <div class='vui-select' :class="{ disabled: disable }"  @blur="showOptions = false" tabindex="0">
        <div class='main' :class="{active: showOptions}" @mouseover="hover = true" @mouseleave="hover = false">
            <div class='value' @click="switchShowOptions">
                <span v-show="label">{{label}}</span>
                <span class='empty' v-show="!label"></span>
            </div>
            <div class='arrow' v-show="!del || !hover" :class='{down: showOptions}' @click="switchShowOptions">
                <v-icon type="ios-arrow-down" />
            </div>
            <div class='del' v-show="del && hover" @click='showOptions = false;$emit("input", "")'>
                <v-icon type="ios-close-circle" />
            </div>
        </div>
        <div class='option-panel' v-show="showOptions">
            <div class='angle'></div>
            <slot></slot>
        </div>
    </div>
</template>
<script>
export default {
    name: "v-select",
    props: {
        value: String,
        disable: Boolean, // 
        del: Boolean, // 
    },
    data(){
        return {
            list: [], // 
            showOptions: false,
            hover: false
        }
    },
    methods: {
        // selectoptionoption
        select(obj){
            // {label, value}
            this.showOptions = false
            this.$emit("input", obj.value)
        },
        //  options
        switchShowOptions(){
            if(this.disable) return
            this.showOptions = !this.showOptions
        }
    },
    computed: {
        label(){
            let labelString = ''
            this.list.map(obj => {
                if(obj.value == this.value){
                    labelString = obj.label
                }
            })
            return labelString
        },
    }
}
</script>
<style lang="scss" scoped>
@import "../../../styles/components/select";
</style>
